// hooks
import { useRef, useEffect } from "react";

// styles
import './mainTitle.css';

export const MainTitle = props => {
    const ref = useRef();

    const ifIsReady = () => {
        if (props.isReady) {
            setTimeout(() => ref.current.classList.add('show-main-title'), 6000);
        }
    };
    
    useEffect(ifIsReady);

    return (
        <div className={`main-title-container ${props.webGLEnabled ? '' : 'main-title-100'}`} id="main-title-container">
            <div ref={ref} className='title-container'>
                <h1 id="main-title">Self-taught frontend developer from Russia</h1>
                <p id="main-description">I love coding and always strive for perfection</p>
            </div>
        </div>
    )
};