// hooks
import { useRef, useState } from 'react';

// components
import { MainScene } from './Components/MainScene/MainScene';
import { Navigation } from './Components/Navigation/Navigation';
import { MainTitle } from './Components/MainTitle/MainTitle';
import { AboutMe } from './Components/AboutMe/AboutMe';
import { MySkills } from './Components/MySkills/MySkills';
import { MyProjects } from './Components/MyProjects/MyProjects';
import { Footer } from './Components/Footer/Footer';
import { LoadingContainer } from './Components/LoadingContainer/LoadingContainer';
import { Settings } from './Components/Settings/Settings';

// styles
import './App.css';

export const App = () => {
  const mainRef = useRef();
  const [isReady, setIsReady] = useState(false);
  const [needToStart, setNeedToStart] = useState(false);
  const [webGLEnabled, setWebGLEnabled] = useState(false);
  const [observerEnabled, setObserverEnabled] = useState(false);

  const main = document.querySelector('.main');

  function isMainSceneInViewport(el) {
    if (!el) return;
    const rect = el.getBoundingClientRect();

    return rect.bottom > 0;
}

function isVisible(elems, menuElems) {
  elems.forEach((el, i) => {
    const rect = el.getBoundingClientRect();
    const mainRect = main.getBoundingClientRect();
    const relativeTop = rect.top - mainRect.top;

    const twentyFiveVh = window.innerHeight / 100 * 25;

    if (main.scrollTop + window.innerHeight >= main.scrollHeight) {
      menuElems[menuElems.length - 1].classList.add('selected-menu-item');
      menuElems[menuElems.length - 2].classList.remove('selected-menu-item');
      return;
    }

    if (relativeTop <= twentyFiveVh) {
      menuElems[i].classList.add('selected-menu-item');
    }

    if (el.classList.contains('my-skills-wrapper') && relativeTop >= window.innerHeight / 2 - twentyFiveVh) {
      menuElems[i].classList.add('selected-menu-item');
    }
  
    if (Math.abs(relativeTop - twentyFiveVh) >= rect.height || relativeTop > twentyFiveVh) {
      menuElems[i].classList.remove('selected-menu-item');
    }
  });
}

function isBottomSceneInViewport(el) {
  if (!el) return;
  const rect = el.getBoundingClientRect();

  return rect.top < window.innerHeight;
}

let doesNotSupportIO = false;

const projectsTitle = document.querySelector('.my-projects-title');
  
const elements = document.querySelectorAll(`.top-wrapper, .about-me-container, .my-skills-wrapper, 
.my-projects-list-item, .bottom-scene-container`);

if (window.IntersectionObserver && observerEnabled) {
  const hasClass = (el, cls) => el.classList.contains(cls);
  const toggle = (el, cls, isIntersecting) => el.classList.toggle(cls, isIntersecting);
  
  const startAnimation = entries => {
    entries.forEach(entry => {
      const {target, isIntersecting} = entry;

      if (hasClass(target, 'left-translation')) {

        toggle(target, 'show-left-translation-anim', isIntersecting);
  
      } else if (hasClass(target, 'right-translation')) {
        
        toggle(target, 'show-right-translation-anim', isIntersecting);
  
      } else if (hasClass(target, 'first-project')) {
  
        toggle(projectsTitle, 'show-el', isIntersecting);
        toggle(target, "show-el", isIntersecting);
        
      } else {
  
        toggle(target, "show-el", isIntersecting);
  
      }
    });
  };

  const options = { root: null, rootMargin: '0px', threshold: 0};

  const observer = new IntersectionObserver(startAnimation, options);

  elements.forEach(el => {
    observer.observe(el, options);
  });
} else {
  doesNotSupportIO = true;
}

const desc = document.getElementById('loading-description');

  if (isReady) {
    const elems = document.querySelectorAll('#main-top-wrapper, .my-skills-main-container, #my-projects-container, #footer');
    const menuElems = document.querySelectorAll('.navlink');

    const highlightMenuItem = () => isVisible(elems, menuElems);
    setInterval(highlightMenuItem, 250);
    
    const part = document.querySelector('.loader-part');
    const value = document.getElementById('loader-value');

    if (value.textContent !== '100%') {
      part.style.transform = 'scaleX(1)';
      value.textContent = '100%';
    }

    if (doesNotSupportIO) {
      elements.forEach(el => {
        if (el.classList.contains('my-skills-wrapper')) {
          el.style.top = '0';
        }
        el.style.opacity = '1';
        el.style.visibility = 'visible';
      });
      projectsTitle.style.opacity = '1';
      projectsTitle.style.visibility = 'visible';
    }

    const waitTime = 4050;
    const now = Date.now();

    const showLeftTime = () => {
      const timePassed = Date.now() - now;
      const timeLeft = (waitTime - timePassed) / 1000;

      if (Date.now() - now >= waitTime || timeLeft.toFixed(0) === '0') {
        desc.textContent = `Ready!`;
        clearInterval(interval);
        return;
      }

      desc.textContent = `Preparing ${(timeLeft.toFixed(0)).split('.').join(',')}s left...`;
    };

    const interval = setInterval(showLeftTime, 1000);
    showLeftTime();

    const projectsContainer = document.querySelector('.my-projects-container');

    setTimeout(() =>  main.scrollTo({top: 0, behavior: 'auto'}), 0);
    setTimeout(() =>  projectsContainer.classList.add('relative-pos'), 3500);

    setTimeout(() => {
      main.classList.add('smooth-scroll');
      main.classList.add('enable-pointer-events');
    }, 4400);
  }
  
  return (
    <div className='App'>
      <div className='background'></div>
      <div className='main' ref={mainRef} id='main'>
      <Settings setObserverEnabled={setObserverEnabled} setIsReady={setIsReady} setWebGLEnabled={setWebGLEnabled} setNeedToStart={setNeedToStart} />
      <LoadingContainer needToStart={needToStart} isReady={isReady} />
      <Navigation isReady={isReady} />
      <div className='main-top-wrapper' id='main-top-wrapper'>
        <div className={`top-wrapper ${webGLEnabled && document.getElementById('c') ? '' : 'vh-100'}`} id='top-wrapper'>
          <MainTitle webGLEnabled={webGLEnabled} isReady={isReady} />
          {window.WebGLRenderingContext && webGLEnabled && <MainScene needToStart={needToStart} isMainSceneInViewport={isMainSceneInViewport} isReady={isReady} setIsReady={setIsReady} />}
        </div>
        <AboutMe />
      </div>
      <MySkills />
      <MyProjects isReady={isReady} />
      <Footer webGLEnabled={webGLEnabled} isReady={isReady} isBottomSceneInViewport={isBottomSceneInViewport} />
      </div>
    </div>
  );
};
