// hooks
import { useState } from "react";

// icons
import codeIcon from '../../images/code.png';

// styles
import './mySkills.css';

export const MySkills = () => {
    const [skills] = useState([
      {name: 'HTML', id: 1, rating: new Array(5).fill(1)},
      {name: 'CSS', id: 2, rating: new Array(5).fill(1)},
      {name: 'JavaScript', id: 3, rating: new Array(4).fill(1)},
      {name: 'React', id: 4, rating: new Array(3).fill(1)},
      {name: 'Redux-toolkit', id: 5, rating: new Array(3).fill(1)},
      {name: 'Material-ui', id: 6, rating: new Array(3).fill(1)},
      {name: 'SCSS', id: 7, rating: new Array(3).fill(1)},
      {name: 'React Native', id: 8, rating: new Array(2).fill(1)},
      {name: 'jQuery', id: 9, rating: new Array(2).fill(1)},
      {name: 'TypeScript', id: 10, rating: new Array(2).fill(1)},
    ]);

    return <div className="my-skills-main-container" id="main-skills-container">
      <div className="my-skills-wrapper" id="my-skills-wrapper">
        <div className="my-skills-info-container">
            <h3 id="my-skills-title">My Skills</h3>
            <table>
            <thead>
              <tr>
                <th>Skills</th>
                <th>Beginner</th>
                <th>Elementary</th>
                <th>Intermediate</th>
                <th>Upper-Intermediate</th>
                <th>Advanced</th>
              </tr>
            </thead>
            <tbody>
              {skills.map(skill => {
                return <tr key={skill.name + skill.rating.length}>
                  <th>{skill.name}</th>

                  {skill.rating.map((num, i) => {
                    return <td key={skill.name + i + ' cell'}><img src={codeIcon} className="skill-rating-icon" alt="code" /></td>
                  })}

                </tr>
              })}
            </tbody>
            <tfoot></tfoot>
          </table>
        </div>
        </div>
    </div>
};