// hooks
import { useEffect, useRef } from "react";

// styles
import './navigation.css';

export const Navigation = props => {
    const ref = useRef();

    const ifIsReady = () => {
        if (props.isReady) {
            Array.from(ref.current.children).forEach(child => {
                setTimeout(() => child.children[0].classList.add('show-text'), 5600);
            });
        }
    };
    
    useEffect(ifIsReady);

    return (
        <nav>
            <ul ref={ref} className="nav-list">
                <li><a className='navlink' href="#top-wrapper">About</a></li>
                <li><a className='navlink' href="#main-skills-container">Skills</a></li>
                <li><a className='navlink' href="#my-projects-title">Projects</a></li>
                <li><a className='navlink' href="#bottom-container">Contact</a></li>
            </ul>
        </nav>
    )
};