// hooks
import { useEffect, useState, useRef } from "react";

// threejs
import * as THREE from 'three';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls';

// models
import model from '../../models/Offensive Idle.glb';

// styles
import './mainScene.css';

export const MainScene = props => {
    const {isMainSceneInViewport} = props;
    const [dimensions] = useState({width: 700, height: 300});
    const ref = useRef();

    const init = () => {
      createMainScene();
      setTimeout(() => {
        if (props.isReady) ref.current.classList.add('show-c1');
      }, 4500);
    };

    useEffect(init);

    function createMainScene() {
      let lostContext = false;

      let mixer, clock = new THREE.Clock();
      const canvas = ref.current;

      function handleContextLost(e) {
        e.preventDefault();
        lostContext = true;
        window.removeEventListener('resize', onWindowResize);
        canvas.remove();
        props.setIsReady(true);
      }

      canvas.addEventListener("webglcontextlost", handleContextLost, false);
      
      const titleContainer = document.querySelector('.main-title-container');
      const nav = document.querySelector('nav');

      const PI = Math.PI;

      let width = window.innerWidth;
      let w = width <= 768 ? width : width / 100 * 55;
      const h = window.innerHeight;

      const ratio = h / width;

      if (ratio > 1 && width > 768 && h > 1030) {
        w = width;
        canvas.classList.add('canvas-width-100');
        titleContainer.classList.add('main-title-100');  
        nav.classList.add('centered-nav');
      } else {
        canvas.classList.remove('canvas-width-100');
        titleContainer.classList.remove('main-title-100');
        nav.classList.remove('centered-nav');
      }

      const WebGlRenderer = window.WebGL2RenderingContext ? THREE.WebGLRenderer : THREE.WebGL1Renderer;

      const renderer = new WebGlRenderer({
        canvas,
        antialias: true,
        alpha: true,
        premultipliedAlpha: false,
      });

      renderer.setSize(w, h);
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.outputEncoding = THREE.sRGBEncoding;
      renderer.shadowMap.enabled = false;

      const fov = 60;
      const aspect = w / h;
      const near = 16;
      const far = 26;

      const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
      camera.position.set(-1.5, 4.5, 20.25);

      const controls = new OrbitControls(camera, canvas);
      controls.update();

      controls.enableZoom = false;
      controls.enableRotate = false;
      controls.enablePan = false;

      const scene = new THREE.Scene();

      scene.background = null;

      function onError() {
        lostContext = true;
        window.removeEventListener('resize', onWindowResize);
        canvas.remove();
        props.setIsReady(true);
      }

      const total = 3812124;
      const val = document.getElementById('loader-value');
      const part = document.querySelector('.loader-part');

      const loader = new GLTFLoader();

      function onProgress(e) {
        if (props.isReady) return;
        const loaded = e.loaded / total;
        val.textContent = (loaded * 100).toFixed(0)+ '%';
        part.style.transform = `scaleX(${loaded})`;
    }

      if (val && part && props.needToStart) {
        loader.load(model, onModelLoaded, onProgress, onError);
      }
      
      let modelWasLoaded;

      function onModelLoaded(GLTF) {
        const root = GLTF.scene;

        root.rotation.set(-PI / 360, -PI / 60, 0);
        root.position.set(0, -11, 0);
        root.scale.set(10, 10, 10);

        mixer = new THREE.AnimationMixer( root );

        const animations = GLTF.animations;
        const animation = animations[ 0 ];

        const offensiveIddle = mixer.clipAction( animation );
        offensiveIddle.play();

        modelWasLoaded = true;
        setRendererSize(width, w, h);

        scene.add(root);

        onWindowResize();

        setTimeout(render, 3500);
        props.setIsReady(true);
      }

      {
        const light = new THREE.SpotLight(0xFFFFFF, .85);
        scene.add(new THREE.AmbientLight(0xFFFFFF, .25));
        light.castShadow = light.receiveShadow = false;
        light.position.set(0, 3, 10);
        light.target.position.set(0, 0, 0);
        light.angle = PI / 2;

        scene.add(light);
      }

      function setRendererSize(inWidth, cnvsWidth, height) {
        canvas.classList.remove('canvas-width-100');
        titleContainer.classList.remove('main-title-100');
        nav.classList.remove('centered-nav');

        if (cnvsWidth >= 650 || height > 750) {
          camera.position.set(-1.5, 4, 21);
          camera.near = 17;
          camera.far = 27;
        }
        
        if (cnvsWidth <= 510 && cnvsWidth !== inWidth) {
          camera.position.set(-1.65, 4.5, 23);
          camera.near = 19;
          camera.far = 29;
        }

        if (cnvsWidth <= 465 && cnvsWidth !== inWidth) {
          camera.position.set(-1.8, 4.5, 27);
          camera.near = 23;
          camera.far = 33;
        }
        
        if (height < 750) {
          camera.position.set(-1.5, 4, 20.4);
          camera.near = 16;
          camera.far = 27;
        }

        if (height < 600) {
          camera.position.set(-1, 4.5, 13);
          camera.near = 9;
          camera.far = 19;
        }

        if (height < 400) {
          camera.position.set(-0.75, 5, 10);
          camera.near = 6;
          camera.far = 16;
        }

        const ratio = height / inWidth;

        if (ratio > 1 && inWidth > 768 && height > 1030) {
          camera.position.set(-1.5, 3, 19.25);
          camera.near = 15;
          camera.far = 26;
          canvas.classList.add('canvas-width-100');
          titleContainer.classList.add('main-title-100'); 
          nav.classList.add('centered-nav');
        }
      }

      function onWindowResize() {
        let width = window.innerWidth;
        let w = width <= 768 ? width : width / 100 * 55;
        let height = window.innerHeight;

        if (height / width > 1 && width > 768 && height > 1030) {
          w = width;
        }

        if (modelWasLoaded) {
          setRendererSize(width, w, height);
        }

        renderer.setSize(w, height);
        renderer.setPixelRatio(window.devicePixelRatio);
        camera.aspect = w / height;
        camera.updateProjectionMatrix();
      };

      window.addEventListener('resize', onWindowResize);

      function render() {
        if (lostContext) return;

        if ( isMainSceneInViewport(canvas) ) {
          let mixerUpdateDelta = clock.getDelta();

          mixer.update( mixerUpdateDelta );
          renderer.render(scene, camera);
        }

        requestAnimationFrame(render);
      }
    }

    return <canvas ref={ref} id="c" width={dimensions.width} height={dimensions.height}>Your browser does not support canvas tag</canvas>
};