// styles
import './settings.css';

export const Settings = props => {
    const $ = selector => document.querySelector(selector);

    const supportsWebGL = window.WebGLRenderingContext;
    const supportsObserver = window.IntersectionObserver;

    if (!supportsWebGL && !supportsObserver) {
        props.setObserverEnabled(false);
        props.setWebGLEnabled(false);
        setTimeout(() => props.setIsReady(true), 500);

        props.setNeedToStart(true);
        
        return null;
    }

    const texts = [
        `Sometimes devices don't have enough power to render 3D in a browser, even if they support WebGL. 
However, if you're using a PC / laptop / the latest versions of iPhone / iPad, you should enable it. 
Otherwise, if you're using older devices such as iPad 2 / iPad 3 / iPhone 5 or a very old laptop, then I recommend you to disable it.
You can turn on the Intersection Observer API, it's responsible for info appearance animations on your screen.
In any case, you can turn on all the toggle switches and check if the app works correctly or not.
If you encounter any problems, then you can simply restart my project and turn off all the toggle switches.`,

        `Sometimes devices don't have enough power to render 3D in a browser, even if they support WebGL. 
However, if you're using a PC / laptop / the latest versions of iPhone / iPad, you should enable it. 
Otherwise, if you're using older devices such as iPad 2 / iPad 3 / iPhone 5 or a very old laptop, then I recommend you to disable it.
In any case, you can turn on the toggle switch and check if the app works correctly or not.
If you encounter any problems, then you can simply restart my project and turn off the toggle switch.`,

        `You can turn on the Intersection Observer API, it's responsible for info appearance animations on your screen.
In any case, you can do that and check if the app works correctly or not.
If you encounter any problems, then you can simply restart my project and turn off the toggle switch.`
    ];

    const onSwitchCircleClicked = (e, btn, description) => {
        const el = e.target;
        if (!el.classList.contains('switch-circle')) return;
        
        el.classList.toggle('circle-enabled');
        $(btn).classList.toggle('switch-btn-enabled');
        $(description).classList.toggle('setting-enabled');
    };

    const whatIsThatContainerToggleDisplay = (e, action, c1, c2) => {
        $(c1).classList[action]('hide-block');
        $(c2).classList[action]('show-block');
    };

    const onRunTheAppBtnClicked = () => {
        const webGL = $('.webGL-btn');
        const observer = $('.observer-btn');

        if (webGL) {

            if (!webGL.classList.contains('switch-btn-enabled')) {
                props.setWebGLEnabled(false);
                setTimeout(() => props.setIsReady(true), 750);
            } else {
                props.setWebGLEnabled(true);
            }

        } else {
            setTimeout(() => props.setIsReady(true), 750);
        }

        if (observer) {

            if (!observer.classList.contains('switch-btn-enabled')) {
                props.setObserverEnabled(false);
            } else {
                props.setObserverEnabled(true);
            }

        }

        props.setNeedToStart(true);
        
        $('.settings-main-container').remove();
    };

    return <div className="settings-main-container">
        <div className="settings-container">
            {
                supportsWebGL && (
                    <div className="switch-btn-container">
                        <p className='switch-btn-description webGL-btn-description setting-enabled'>WebGL</p>
                        <div className="switch-btn webGL-btn switch-btn-enabled">
                            <p className="switch-btn-on"></p>
                            <button onClick={e => onSwitchCircleClicked(e, '.webGL-btn', '.webGL-btn-description')} className="switch-circle circle-enabled"></button>
                            <p className="switch-btn-off"></p>
                        </div>
                    </div>
                )
            }
            {
                supportsObserver && (
                    <div className="switch-btn-container">
                        <p className='switch-btn-description observer-btn-description setting-enabled'>Intersection Observer API</p>
                        <div className="switch-btn observer-btn switch-btn-enabled">
                            <p className="switch-btn-on"></p>
                            <button onClick={e => onSwitchCircleClicked(e, '.observer-btn', '.observer-btn-description')} className="switch-circle circle-enabled"></button>
                            <p className="switch-btn-off"></p>
                        </div>
                    </div>
                )
            }
            <button onClick={e => whatIsThatContainerToggleDisplay(e, 'add', '.settings-container', '.what-is-that-container')} className="settings-info-btn">What is that?</button>
            <button onClick={onRunTheAppBtnClicked} className="run-the-app-btn">Run the app</button>
        </div>
        <div className="what-is-that-container">
            <h2 id="what-is-that-title">The app requires {supportsWebGL && 'WebGL'} {supportsWebGL && supportsObserver && 'and'} {supportsObserver && 'Intersection Observer API'}</h2>
            <p id="what-is-that-description">{ supportsWebGL && supportsObserver ? texts[0] : supportsWebGL ? texts[1] : supportsObserver ? texts[2] : '' }</p>
            <button onClick={e => whatIsThatContainerToggleDisplay(e, 'remove', '.settings-container', '.what-is-that-container')} className="what-is-that-btn">OK, got it!</button>
        </div>
    </div>
};