// hooks
import { useState, useEffect, useRef } from "react";

// threejs
import * as THREE from 'three';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls';

// models
import Waving from '../../models/Waving.glb';

// styles
import './bottomScene.css';

export const BottomScene = props => {
    const {isBottomSceneInViewport} = props;
    const [dimensions] = useState({width: 700, height: 300});
    const ref = useRef();

    const WebGLIsAvailable = window.WebGLRenderingContext && props.webGLEnabled;

    function init() {
      if (WebGLIsAvailable) createBottomScene();
    }

    useEffect(init);

    function createBottomScene() {
      let lostContext = false;
      let mixer, clock = new THREE.Clock();
      const canvas = ref.current;
      const bottomTitle = document.querySelector('.bottom-title');

      function handleContextLost(e) {
        e.preventDefault();
        lostContext = true;
        window.removeEventListener('resize', onWindowResize);
        canvas.remove();
        bottomTitle.parentElement.classList.add('no-canvas');
      }

      canvas.addEventListener("webglcontextlost", handleContextLost, false);

      const PI = Math.PI;

      let width = window.innerWidth;
      let w = width <= 1280 ? width : width / 100 * 40;
      let h = window.innerHeight * 0.8;

      const ratio = window.innerWidth / window.innerHeight;

      if (ratio < 1.24 && window.innerHeight > 1060 && window.innerWidth > 1280) {
        w = window.innerWidth;
        bottomTitle.classList.add('bottom-title-100');
      } else {
        bottomTitle.classList.remove('bottom-title-100');
      }

      const WebGlRenderer = window.WebGL2RenderingContext ? THREE.WebGLRenderer : THREE.WebGL1Renderer;

      const renderer = new WebGlRenderer({
        canvas,
        antialias: true,
        alpha: true,
        premultipliedAlpha: false,
      });

      renderer.setSize(w, h);
      renderer.setPixelRatio(window.devicePixelRatio);
      renderer.outputEncoding = THREE.sRGBEncoding;
      renderer.shadowMap.enabled = false;

      const fov = 45;
      const aspect = w / h;
      const near = 14;
      const far = 36;

      const camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
      camera.position.set(-1, 14, 21);

      const controls = new OrbitControls(camera, canvas);
      controls.update();

      controls.enableZoom = false;
      controls.enableRotate = false;
      controls.enablePan = false;

      const scene = new THREE.Scene();
      scene.background = null;

      function onError() {
        lostContext = true;
        window.removeEventListener('resize', onWindowResize);
        canvas.remove();
        bottomTitle.parentElement.classList.add('no-canvas');
      }

      const loader = new GLTFLoader();
      if (props.isReady) {
        loader.load(Waving, onModelLoaded, null, onError);
      }

      let modelWasLoaded;

      function onModelLoaded(GLTF) {
        const root = GLTF.scene;

        root.rotation.set(-PI / 10, -PI / 2.2, 0);
        root.position.set(1.3, -11, 0);
        root.scale.set(10, 10, 10);

        mixer = new THREE.AnimationMixer( root );

        const animations = GLTF.animations;
        const animation = animations[ 0 ];

        const waving = mixer.clipAction( animation );
        waving.play();

        modelWasLoaded = true;
        setRendererSize(width, w, h);

        scene.add(root);

        onWindowResize();
        
        let now = Date.now(), requestID = null;

        const preload = () => {
          if (Date.now() - now >= 2250) {
            setTimeout(render, 1500);
            cancelAnimationFrame(requestID);
            return;
          }

          let mixerUpdateDelta = clock.getDelta();

          mixer.update( mixerUpdateDelta );
          renderer.render(scene, camera);

          requestID = requestAnimationFrame(preload);
        }

        setTimeout(preload, 250);
      }

      {
        const light = new THREE.SpotLight(0xFFFFFF, .85);
        scene.add(new THREE.AmbientLight(0xFFFFFF, .25));
        light.castShadow = light.receiveShadow = false;
        light.position.set(0, 10, 15);
        light.target.position.set(0, 5, -10);
        light.angle = PI / 2;

        scene.add(light);
      }

      function setRendererSize(inWidth, cnvsWidth, height) {
        bottomTitle.classList.remove('bottom-title-100');

        if (cnvsWidth >= 440 || height >= 750) {
            camera.position.set(0, 11.5, 16.5);
            camera.near = 12;
            camera.far = 28;      
        }

        if (height < 750) {
          camera.position.set(0, 12, 17);
          camera.near = 13;
          camera.far = 29;
        }

        if (height < 500) {
          camera.position.set(0, 11.5, 12);
          camera.near = 8;
          camera.far = 24;
        }

        if (height < 400) {
          camera.position.set(0, 10.75, 8);
          camera.near = 4;
          camera.far = 20;
        }

        const h = window.innerHeight;

        const ratio = inWidth / h;

        if (ratio < 1.24 && h > 1060 && inWidth > 1280) {
          bottomTitle.classList.add('bottom-title-100');
        }
      }

      function onWindowResize() {
        let width = window.innerWidth;
        let w = width <= 1280 ? width : width / 100 * 40;
        let height = window.innerHeight * 0.8;

        const ratio = window.innerWidth / window.innerHeight;

        if (ratio < 1.24 && window.innerHeight > 1060 && window.innerWidth > 1280) {
          w = window.innerWidth;
        }

        if (modelWasLoaded) {
          setRendererSize(width, w, height);
        }

        renderer.setSize(w, height);
        renderer.setPixelRatio(window.devicePixelRatio);
        camera.aspect = w / height;
        camera.updateProjectionMatrix();
      };

      window.addEventListener('resize', onWindowResize, false);

      function render() {
        if (lostContext) return;

        if ( isBottomSceneInViewport(ref.current) ) {
            let mixerUpdateDelta = clock.getDelta();

            mixer.update( mixerUpdateDelta );
            renderer.render(scene, camera);
        }

        requestAnimationFrame(render);
      }
    }

    return <div className={`bottom-scene-container ${WebGLIsAvailable ? 'with-canvas' : 'no-canvas'}`} id="bottom-container">
        <h3 className="bottom-title">It was a pleasure to meet you!</h3>
      {WebGLIsAvailable && <canvas className="bottom-scene" ref={ref} id="c2" width={dimensions.width} height={dimensions.height}></canvas>}
    </div>
};