// hooks
import { useState } from "react";

// components
import { MyProjectItem } from "../MyProjectItem/MyProjectItem";

// images
import funpizzaWeb from '../../images/funpizza-web.jpg';
import jenga from '../../images/jenga.jpg';
import reflection from '../../images/reflection.jpg';
import paint from '../../images/paint.png';
import hny20 from '../../images/HNY2020.jpg';
import iPhone from '../../images/iPhone.jpg';
import weatherForecast from '../../images/weather_forecast.jpg';
import funpizzaMobile from '../../images/Funpizza_mobile.jpg';
import calculator from '../../images/calculator.png';
import cssFish from '../../images/css-fish.jpg';
import speechRecognition from '../../images/Speech-recognition.jpg';
import photoGallery from '../../images/Photo-gallery.jpg';

// styles
import './myProjects.css';

export const MyProjects = props => {

    const [projects] = useState([
        {
            src: funpizzaWeb,
            id: 1,
            name: 'Funpizza',
            description: 'Awesome app where you can enjoy delicious food',
            projectUrl: 'https://dnt-knw.github.io/Funpizza/',
        },

        {
            src: photoGallery,
            id: 2,
            name: 'Photo gallery',
            description: 'Cool app that allows you to store your photos',
            projectUrl: 'https://dnt-knw.github.io/Photo-gallery',
        },

        {
            src: weatherForecast,
            id: 3,
            name: 'Weather forecast',
            description: 'Beautifully designed app with animated background',
            projectUrl: 'https://dnt-knw.github.io/Weather-forecast/',
        },

        {
            src: iPhone,
            id: 4,
            name: 'iPhone clone',
            description: 'Animated UI clones of iPhone widgets',
            projectUrl: 'https://dnt-knw.github.io/iPhone-clone/',
        },

        {
            src: paint,
            id: 5,
            name: 'Paint',
            description: 'Paint app with the repetition of your drawing',
            projectUrl: 'https://dnt-knw.github.io/Paint/',
        },

        {
            src: reflection,
            id: 6,
            name: 'Realistic reflection',
            description: 'Flying reflective cubes and a reflective sphere',
            projectUrl: 'https://dnt-knw.github.io/Realistic-reflection/',
        },

        {
            src: jenga,
            id: 7,
            name: 'Jenga',
            description: 'Interaction with jenga blocks with the help of physics',
            projectUrl: 'https://dnt-knw.github.io/Jenga/',
        },

        {
            src: calculator,
            id: 8,
            name: 'Calculator',
            description: 'Just a useful and nice looking calculator',
            projectUrl: 'https://dnt-knw.github.io/Calculator/',
        },

        {
            src: hny20,
            id: 9,
            name: 'Happy New Year',
            description: "Code with a great New Year's atmosphere",
            projectUrl: 'https://dnt-knw.github.io/Happy-New-Year/',
        },

        {
            src: cssFish,
            id: 10,
            name: 'CSS Fish',
            description: 'Beautiful fish animation using pure css',
            projectUrl: 'https://dnt-knw.github.io/CSS-fish/',
        },

        {
            src: funpizzaMobile,
            id: 11,
            name: 'Funpizza mobile',
            description: 'Awesome mobile app where you can enjoy delicious food',
            projectUrl: 'https://expo.dev/@dddwww/funpizza?serviceType=classic&distribution=expo-go',
        },

        {
            src: speechRecognition,
            id: 12,
            name: 'Speech recognition',
            description: 'Useful app with speech synthesis and recognition features',
            projectUrl: 'https://dnt-knw.github.io/Speech-recognition/',
        },
    ]);

    return <div className="my-projects-container" id="my-projects-container">
        <h2 className='my-projects-title' id="my-projects-title">My Projects</h2>
        <ul className="my-projects-list">
            {
                projects.map(project => {
                    const isEven = project.id % 2 === 0;
                    const isFirst = project.id === 1;
                    const needTransform = project.id !== 1 && project.id !== projects.length;
                    return <MyProjectItem isReady={props.isReady} needTransform={needTransform} isEven={isEven} key={project.id} src={project.src}
                    name={project.name} isFirst={isFirst} description={project.description} projectUrl={project.projectUrl} />
                })
            }
        </ul>
    </div>
};