// hooks
import { useRef } from "react";

// styles
import './myProjectItem.css';

export const MyProjectItem = props => {
    const linkRef = useRef();
    const descRef = useRef();
    const imgRef = useRef();

    const prepare = (el, cls, action, ms) => setTimeout(() => el.classList[action](cls), ms);

    const interval = setInterval(() => {
        if (descRef.current && props.isReady) {
            clearInterval(interval);
            const el = descRef.current;
            const img = imgRef.current;

            prepare(el, 'description-transform', 'add', 500);
            prepare(el, 'description-transform', 'remove', 1250);

            prepare(img, 'img-transform', 'add', 500);
            prepare(img, 'img-transform', 'remove', 1250);

            prepare(el, 'description-transform', 'add', 2000);
            prepare(el, 'description-transform', 'remove', 2750);

            prepare(img, 'img-transform', 'add', 2000);
            prepare(img, 'img-transform', 'remove', 2750);
        }
    }, 100);

    const touchScreen = 'ontouchmove' in document;

    const disableLink = () => {
        if (linkRef.current.classList.contains('enabled-link')) {
            linkRef.current.classList.remove('enabled-link');
            setTimeout(() => linkRef.current.classList.add('disabled-link'), 0);
        }
    }

    const enableLink = () => {
        linkRef.current.classList.remove('disabled-link');
        if (!linkRef.current.classList.contains('enabled-link')) {
            setTimeout(() => linkRef.current.classList.add('enabled-link'), 0);
        }
    }

    let tapped = false;

    const onTransitionEnd = () => {
        if (touchScreen) enableLink();
    };

    const onMouseEvent = () => {
        if (touchScreen) disableLink();
    };

    const onMouseEnter = () => {
        onMouseEvent();
    };

    const onMouseLeave = () => {
        onMouseEvent();
        tapped = false;
    };

    const onTouchStart = () => {
        if (tapped) return; 
        onMouseEvent();
        tapped = true;
    }

    return <li onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} onTouchStart={onTouchStart}
     className={`my-projects-list-item ${props.needTransform ? props.isEven ? 'left-translation' : 'right-translation' : ''} 
    ${props.isFirst ? 'first-project' : ''}`}>
        <img ref={imgRef} src={props.src} className='my-projects-image' alt="project-img" unselectable="on" />
        <div ref={descRef} onTransitionEnd={onTransitionEnd} className="project-description-main-container">
            <h3 className="my-project-name">{props.name}</h3>
            <div className="project-description-container">
                <p className="my-project-description">{props.description}</p>
                <a ref={linkRef} href={props.projectUrl} className={`${touchScreen ? 'disabled-link' : 'enabled-link'}`} target='_blank' rel="noreferrer" ><button className="my-project-open-btn">Open</button></a>
            </div>
        </div>
    </li>
};