// components
import { BottomScene } from "../BottomScene/BottomScene";

// icons
import mailIcon from '../../images/mail_icon.png';
import telegramIcon from '../../images/telegram_icon.png';
import happy from '../../images/happy.png';

// styles
import './footer.css';

export const Footer = props => {
    return <div className="footer" id="footer">
        <BottomScene isReady={props.isReady} webGLEnabled={props.webGLEnabled} isBottomSceneInViewport={props.isBottomSceneInViewport} />
        <div className={`contacts-content-container ${props.webGLEnabled ? '' : 'contacts-no-canvas'}`}>
            <div className="contacts-content-child-container">
                <a className="contacts-link" href="mailto:Sharmazanov99@mail.ru" target='_blank' rel="noreferrer">
                    <img alt="mail" src={mailIcon} className='contacts-icon' />
                    <span className="contacts-value">Sharmazanov99@mail.ru</span>
                </a>
            </div>
            <div className="contacts-content-child-container">
                <a className="contacts-link" href='https://t.me/shrmznv' target='_blank' rel="noreferrer">
                    <img alt="telegram" src={telegramIcon} className='contacts-icon' />
                    <span className="contacts-value">@shrmznv</span>
                </a>
            </div>
        </div>
        <div className="more-projects-container">
            <span className="more-projects-text">More projects are</span>
            <a className="more-projects-link" href="https://www.sololearn.com/profile/15463112" target='_blank' rel="noreferrer">here</a>
        </div>
        <p className="fotter-bottom-text">Coding is fun <img id="smiley" alt="smiley" src={happy} /></p>
    </div>
};