// styles
import './loadingContainer.css';

export const LoadingContainer = props => {
    if (!props.needToStart) return;
    const remove = () => setTimeout(() => {
        const loadingContainer = document.querySelector('.loading-container');
        loadingContainer && loadingContainer.remove();
    }, 5600);
    if (props.isReady) remove();

    return <div className={`loading-container ${props.isReady ? 'hide' : ''}`}>
        <div className="loader-container">
            <div className="loader">
                <div className="loader-part"></div>
                <p id="loader-value">0%</p>
            </div>
            <p id="loading-description">Loading resources...</p>
        </div>
    </div>
};